@import "../../variables.modules";

.pink {
  color: $pink;
}

.green {
  color: $green;
}

.skills {
  columns: 1;
  @media only screen and (min-width: 940px) {
    columns: 2;
  }

  li {
    margin: 0;
    line-height: 1.75;
  }
}

.aboutModern {
  width: 100%;
  padding: 0 1rem;
  background: none;
}

.card {
  border-radius: 18px;
  background: $white;
  box-shadow: 0 2px 16px rgba(0,0,0,0.04);
  margin-bottom: 2rem;
  transition: box-shadow 0.2s, background 0.3s, color 0.3s, border 0.3s;
  color: $dark;
  &:hover {
    box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  }
}

body.dark .card, .dark .card {
  background: $dark !important;
  color: $white !important;
  border: 1px solid $white;
}

body.dark .card .MuiCardContent-root, .dark .card .MuiCardContent-root {
  background: transparent !important;
  color: $white !important;
}

body.dark .card .MuiTypography-root, .dark .card .MuiTypography-root {
  color: $white !important;
}

.lastName {
  color: $pink;
  font-weight: 700;
}

.skillsList {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.skillChip {
  font-size: 0.95rem;
  border-radius: 8px;
  background: rgba($pink, 0.08);
  color: $dark;
  transition: background 0.2s, color 0.2s, border 0.2s;
  box-shadow: 0 1px 4px rgba(0,0,0,0.04);
}

.hobbiesList {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    display: flex;
    align-items: center;
    font-size: 1rem;
    margin-bottom: 0.5rem;
    color: $dark;
    background: rgba($pink, 0.07);
    border-radius: 6px;
    padding: 0.3rem 0.7rem;
    transition: background 0.2s, color 0.2s;
    box-shadow: 0 1px 4px rgba(0,0,0,0.04);
    &:hover {
      background: rgba($pink, 0.15);
    }
  }
}

/* Dark mode styles - opravdu černé karty a bílý text */
.dark .aboutModern,
body.dark .aboutModern {
  background: linear-gradient(135deg, #10131a 0%, #1a2233 100%);
}

.dark .aboutModern .card,
body.dark .aboutModern .card {
  background: #000 !important;
  color: #fff !important;
  border: 1.5px solid #2e3748;
  box-shadow: 0 4px 32px rgba(0,0,0,0.55);
}

.dark .aboutModern .card * {
  color: #fff !important;
}

.dark .aboutModern .lastName,
body.dark .aboutModern .lastName {
  color: $pink !important;
  text-shadow: 0 2px 8px rgba($pink, 0.15);
}

.dark .aboutModern .MuiTypography-h5,
.dark .aboutModern .MuiTypography-h6 {
  color: #fff !important;
  letter-spacing: 0.5px;
  text-shadow: 0 2px 8px rgba(0,0,0,0.18);
}

.dark .aboutModern .MuiDivider-root {
  border-color: #2e3748 !important;
  opacity: 0.7;
}

.dark .aboutModern .skillChip,
body.dark .aboutModern .skillChip {
  background: rgba($pink, 0.32);
  color: #fff !important;
  border: 1.5px solid $pink;
  box-shadow: 0 2px 8px rgba($pink, 0.10);
}

.dark .aboutModern .hobbiesList li,
body.dark .aboutModern .hobbiesList li {
  background: rgba($pink, 0.22);
  color: #fff !important;
  border: 1px solid #2e3748;
  box-shadow: 0 2px 8px rgba($pink, 0.10);
  &:hover {
    background: rgba($pink, 0.38);
  }
}