.contactContainer {
  max-width: 420px;
  margin: 2rem auto;
  padding: 2.5rem 2rem;
  background: var(--contact-bg, #fff);
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.10);
  transition: background 0.3s, color 0.3s;
}

body.dark .contactContainer {
  --contact-bg: #23272f;
  color: #fff;
}

.contactTitle {
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  letter-spacing: 1px;
}

.contactForm label {
  display: block;
  margin-bottom: 0.4rem;
  font-weight: 500;
}

.contactInput, .contactTextarea {
  width: 100%;
  padding: 0.7rem 1rem;
  border: 1.5px solid #d1d5db;
  border-radius: 8px;
  margin-bottom: 1.2rem;
  font-size: 1rem;
  background: transparent;
  transition: border 0.2s, box-shadow 0.2s;
}

body.dark .contactInput, body.dark .contactTextarea {
  border: 1.5px solid #444b5a;
  background: #23272f;
  color: #fff;
}

.contactInput:focus, .contactTextarea:focus {
  border-color: #7c3aed;
  outline: none;
  box-shadow: 0 0 0 2px #c7d2fe;
}

.contactButton {
  width: 100%;
  padding: 0.8rem 0;
  background: linear-gradient(90deg, #7c3aed 0%, #38bdf8 100%);
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s, transform 0.2s;
  box-shadow: 0 2px 8px rgba(60,60,120,0.08);
}

.contactButton:hover {
  background: linear-gradient(90deg, #38bdf8 0%, #7c3aed 100%);
  transform: translateY(-2px) scale(1.03);
}

.contactSuccess {
  text-align: center;
  color: #22c55e;
  font-size: 1.2rem;
  font-weight: 500;
  margin-top: 1.5rem;
}
